import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { backendUrl } from '../../config/config';
import Helmet from 'react-helmet';

import HeaderBlock from './HeaderBlock';
import SetsSlider from '../../components/SetsSlider';
import './index.scss';
import Spinner from '../../components/Spinner';
import ErrorIndicator from '../../components/Errors/ErrorIndicator';
import MainSwiper from '../Main/components/MainSwiper/index';

const onLoadIFrame = (obj) => {
  obj.target.style.height = obj.target.contentWindow.document.documentElement.scrollHeight + 'px';
};

const CardProduct = (props) => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);
  const [product, setProduct] = useState(false);
  const [categoryProducts, setCategoryProducts] = useState([]);

  useEffect(() => {
    const productRoute = props.match.params.route;
    const categoryRoute = props.match.params.catRoute;
    setLoading(true);

    axios
      .post(backendUrl + '/products/by/route', {
        route: productRoute,
      })
      .then((res) => {
        setProduct(res.data.products[0]);
        setLoading(false);
      })
      .catch((err) => {
        setError(true);
        setLoading(false);
      });

    axios
      .post(backendUrl + '/products/category/by/routes', {
        routes: categoryRoute,
      })
      .then((res) => {
        setCategoryProducts(res.data.products);
        setError(false);
        setLoading(false);
      })
      .catch((err) => {
        setError(true);
        setLoading(false);
      });
  }, [props]);

  const createJSONLD = () => {
    if (product) {
      const data = {
        '@context': 'https://schema.org',
        '@type': 'Product',
        description:
          product.name_ru +
          '| ' +
          `${product.name_ru} Доставка. Заказывай ${product.name_ru} с бесплатной доставкой в Днепре, Запорожье, Харькове✔️Суши от 15грн ☎ 050 719-24-24 ✔️Роллы ✔️Сеты ✔️Wok ✔Онлайн заказ ${product.name_ru} на дом,офис.`,
        name: product.name_ru,
        image: backendUrl + '/' + product.image,
        offers: {
          '@type': 'Offer',
          availability: 'https://schema.org/InStock',
          price: product.percent !== 0 ? product.price * ((100 - product.percent) / 100) : product.price,
          priceCurrency: 'UAH',
        },
      };

      return JSON.stringify(data);
    }
  };

  const { city } = props;

  console.log(categoryProducts);

  return (
    <div className={'card-product'}>
      {loading && <Spinner />}
      {error && <ErrorIndicator />}
      {product && categoryProducts && !error && !loading && (
        <>
          <Helmet>
            <title>{city === 1 ? product.seo_title_dp : city === 2 ? product.seo_title_zp : product.seo_title}</title>
            <meta
              name='description'
              content={
                city === 1
                  ? product.seo_description_dp
                  : city === 2
                  ? product.seo_description_zp
                  : product.seo_description
              }
            />
            <meta
              name='keywords'
              content={
                city === 1 ? product.seo_keywords_dp : city === 2 ? product.seo_keywords_zp : product.seo_keywords
              }
            />
            <script async type='application/ld+json'>
              {createJSONLD()}
            </script>
            <script type='application/ld+json'>
              {JSON.stringify({
                '@context': 'http://schema.org',
                '@type': 'WebSite',
                '@id': '#website',
                url: 'https://24rolls.com.ua/',
                name: '\u0421\u0443\u0448\u0438 24Rolls Dnepr',
                potentialAction: {
                  '@type': 'SearchAction',
                  target: 'https://24rolls.com.ua/?s={search_term_string}',
                  'query-input': 'required name=search_term_string',
                },
              })}
            </script>
            <script type='application/ld+json'>
              {JSON.stringify({
                '@context': 'http://schema.org',
                '@type': 'Organization',
                url: 'https://24rolls.com.ua/',
                contactPoint: [
                  {
                    '@type': 'ContactPoint',
                    telephone: '068 719 24 24',
                    contactType: ['customer service', 'sales'],
                    areaServed: ['UA'],
                    contactOption: ['TollFree'],
                    availableLanguage: ['Russian', 'Ukrainian'],
                  },
                ],
                sameAs: [
                  'https://www.youtube.com/channel/UCmJwKEidDraJtVdYtpnsJSA',
                  'https://www.instagram.com/24rolls.com.ua/',
                ],
              })}
            </script>
          </Helmet>
          <HeaderBlock {...props} food={product} />
          <SetsSlider
            {...props}
            list={product.recommend.length ? product.recommend : categoryProducts}
            title={true}
            links={true}
          />
          <div>
            <iframe
              onLoad={onLoadIFrame}
              style={{
                width: '100%',
                border: 'none',
                height: '100%',
                verflow: 'visible',
                fontFamily: "'Roboto', sans-serif",
              }}
              srcDoc={product.article}
            >
              {'Ваш браузер не поддерживает Frame'}
            </iframe>
          </div>
        </>
      )}
    </div>
  );
};

export default CardProduct;
